
.sec_first {
    display: flex;
    justify-content: center;    
    
}
.sec_first_continer{
    margin-top:0px;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;   

   
}

.arrow {
    display: block;
    border-right: 5px solid white;
    border-bottom:  5px solid white;
    margin: 0 auto;  
    width: 50px;
    height: 50px;
    position: absolute;
    bottom: 15%;
    left: calc(50% - 32px);
    animation: moveArrow 1.5s 1s ease-in-out infinite;
    transform: translate(50%, 50%);
    transform: rotate(45deg);
}

@keyframes moveArrow {
    0%{bottom: 10%
    }
    50%{ bottom: 12%
    }
    100% { bottom: 10%
    }
}
.sec_first h1 {
    color: white;
}

.photo {
    margin: 10px;
}

.img_small {
    height: 280px;
    width: 280px;

}

.sec_second { 
    display: flex;
    justify-content: center;
    flex-wrap:  wrap;
} 


.btn-container {
    display: flex;
    justify-content: space-around;
   
}

.btn-primary {
    height: 50px;
