@media (max-width: 768px) {
    .info_panel {
      width: 100%;
      max-width: none;
      padding-left:0px;
      padding-right: 0px;
      
    }
  }
  @media (max-width: 768px) {
    .container {
      width: 100% !important;
      max-width: none;
    }
  }

  @media (max-width:735px) {
      .table-dark {
          display: none;

      }

    
      .display-4 {
          font-size: 28px;
      }
  }
  .span_sol {
      display: none;

  }

  @media (max-width: 433px) {
      
      .pickNumber{
          width: 114px;
          border-radius: 0px
      }
      .choose_sol_day_span {
        display: none;    
        
      }
      .accept {
          width: 64px;
          border-radius: 0px 6px 6px 0px !important;
      }
      .span_sol{
          display: flex;
          width: 90px;
          justify-content: center;
          border-radius: 6px 0px 0px 6px !important;
          
      }
     
    

  }
  @media (max-width:1120px){
    .info_sec {
        background-repeat: repeat-y;
     height: 1250px;
    };
  }
  @media (max-width:632px){
    .info_sec {
        background-repeat: repeat-y;
     height: 2150px;
    }
    .wrap-raw {
        flex-direction: column;
    }
    .chooseSol {
        justify-content: flex-start;
    } 
    .fbox__missionInfo {
        justify-content: flex-start;
        padding-top: 5px;
    }
    #cls {
        
    }
  }
  .btn-group>.btn:active, .btn-group>.btn:focus {
    z-index: 0;
}

